<template>
	<view>
		<image src="../../../static/mine/yaoqing-tou.png" mode="" style="width: 100%;background-color: #7900B2;"
			:style="'height:' + topImgageHeight + 'px;'"></image>
		<scroll-view scroll-y="true" class="inval-scroll"
			:style="'top:' + (topImgageHeight-40) + 'px;height:' + scrollHeight + 'px;'">
			<view class="inval-middleView column">
				<view style="width: 100%;margin-top: 20px;height: 24px;justify-content: center;" class="row">
					<image src="../../../static/mine/yaoqing-left.png" mode=""
						style="height: 18px;width: 30px;margin-top: 3px;margin-right: 10px;"></image>
					<text
						style="height: 24px;line-height: 24px;color: #323232;font-size: 16px;font-weight: bolder;">三步领奖励</text>
					<image src="../../../static/mine/yaoqing-right.png" mode=""
						style="height: 18px;width: 30px;margin-top: 2px;margin-left: 10px;"></image>
				</view>
				<view class="row" style="width: 100%;margin-top: 32px;height: 53px;justify-content: center;">
					<image src="../../../static/mine/yaoqing1.png" mode=""
						style="height: 53px;width: 53px;margin-right: 10px;"></image>
					<image src="../../../static/mine/yaoqing-dian.png" mode=""
						style="height: 4px;width: 32px;margin-right: 10px;margin-top: 24.5px;"></image>
					<image src="../../../static/mine/yaoqing2.png" mode=""
						style="height: 53px;width: 53px;margin-right: 10px;"></image>
					<image src="../../../static/mine/yaoqing-dian.png" mode=""
						style="height: 4px;width: 32px;margin-right: 10px;margin-top: 24.5px;"></image>
					<image src="../../../static/mine/yaoqing3.png" mode=""
						style="height: 53px;width: 53px;margin-right: 10px;"></image>
				</view>
				<view class="row" style="width: 100%;margin-top: 8px;height: 34px;justify-content: center;">
					<text
						style="height: 34px;width: 53px;margin-right: 10px;line-height: 17px;font-size: 13px;color: #7900B2;font-weight: bold;text-align: center;">邀请
						新人好友</text>
					<view style="height: 4px;width: 32px;margin-right: 10px;"></view>
					<text
						style="height: 34px;width: 53px;margin-right: 10px;line-height: 17px;font-size: 13px;color: #7900B2;font-weight: bold;text-align: center;">好友
						下载注册</text>
					<view style="height: 4px;width: 32px;margin-right: 10px;"></view>
					<text
						style="height: 34px;width: 53px;margin-right: 10px;line-height: 17px;font-size: 13px;color: #7900B2;font-weight: bold;text-align: center;">好友
						下单支付</text>
				</view>
				<view @click="doYaoqing"
					style="width: calc(100% - 92px);margin-left: 46px;margin-top: 36px; height: 44px;background-color: #7900B2;border-radius: 22px;font-size: 16px;line-height: 44px;font-weight: bolder;color: #FFFFFF;text-align: center;">
					立即邀请
				</view>
				<view style="width: 100%;margin-top: 15px;font-size: 14px;color: #989898;text-align: center;">
					你已经邀请{{invalNums}}人
				</view>
			</view>

			<view class="inval-tipView column">
				<view style="width: 100%;margin-top: 20px;height: 24px;justify-content: center;" class="row">
					<image src="../../../static/mine/yaoqing-left.png" mode=""
						style="height: 18px;width: 30px;margin-top: 3px;margin-right: 10px;"></image>
					<text
						style="height: 24px;line-height: 24px;color: #323232;font-size: 16px;font-weight: bolder;">领取规则</text>
					<image src="../../../static/mine/yaoqing-right.png" mode=""
						style="height: 18px;width: 30px;margin-top: 2px;margin-left: 10px;"></image>
				</view>
				<view class="inval-tipLabel">
					{{tipWord}}
				</view>
			</view>
			<view style="width: 100%;height: 40px;">

			</view>
		</scroll-view>

		<image src="../../../static/back-white.png" mode=""
			style="position: absolute; width: 30px;height: 30px;top: 50px;left: 14px;" @click="doBack">
		</image>

		<view class="content" style="position: absolute; background-color: rgb(0,0,0,0.2);top: 0;left: 0;"
			v-show="showShare">
			<view
				style="position: absolute;bottom: 0;width: 100%;height: 200px;background-color: #FFFFFF;border-radius: 10px 10px 0;">
				<view
					style="height: 40px;width: 100%;color: #989898;font-size: 15px;line-height: 40px;text-align: center;">
					分享至
				</view>
				<view
					style="width: 100%;margin-top: 17px;height: 83px;justify-content: center;border-bottom: 1px solid #f7f8f9;"
					class="row">
					<view style="height: 100%;width: 50px;margin-right: 55px;" class="column" @click="doShare('weixin')">
						<image src="../../../static/mine/share-wx.png" mode=""
							style="margin-left: 5px;width: 40px;height: 40px;"></image>
						<text
							style="margin-top: 10px;font-size: 12px;color: #989898;text-align: center;width: 100%;">微信好友</text>
					</view>
					<view style="height: 100%;width: 50px;margin-right: 55px;" class="column" @click="doShare('qq')">
						<image src="../../../static/mine/share-qq.png" mode=""
							style="margin-left: 5px;width: 40px;height: 40px;"></image>
						<text
							style="margin-top: 10px;font-size: 12px;color: #989898;text-align: center;width: 100%;">QQ好友</text>
					</view>
					<view style="height: 100%;width: 50px;" class="column" @click="doHaibao()">
						<image src="../../../static/mine/share-haibao.png" mode=""
							style="margin-left: 5px;width: 40px;height: 40px;"></image>
						<text
							style="margin-top: 10px;font-size: 12px;color: #989898;text-align: center;width: 100%;">海报</text>
					</view>
				</view>
				<view style="margin-top: 15px;font-size: 15px;color: #323232;text-align: center;width: 100%;"
					@click="quxiaoShare">取消</view>

			</view>
		</view>
	</view>

</template>

<script>
	export default {
		data() {
			return {
				windowWidth: 0,
				scrollHeight: 0,
				topImgageHeight: 0,
				invalNums: 0,
				showShare: 0,
				tipWord:'',
				code:''
			}
		},
		onLoad() {
			var that = this
			that.windowWidth = getApp().globalData.windowWidth
			that.topImgageHeight = that.windowWidth * 0.75
			that.scrollHeight = getApp().globalData.windowHeight - that.topImgageHeight + 40
			that.http.sendRequest('Agent.MyAgent', {}).then(function(res) {
				if (res.code == 0) {
					that.invalNums = res.info[0].nums
					that.tipWord = res.info[0].tips
					that.code = res.info[0].code
				}
			})
		},
		methods: {
			doBack() {
				uni.navigateBack({

				})
			},
			doYaoqing() {
				
				// #ifdef APP-PLUS
				this.showShare = 1
				// #endif
				// #ifdef MP-WEIXIN
				this.doHaibao()
				// #endif
			},
			quxiaoShare() {
				this.showShare = 0
			},
			doShare(provider){
				uni.share({
				    provider: provider,
				    scene: "WXSceneSession",
				    type: 0,
				    href: getApp().globalData.weburl + 'appapi/share/index?code=' + this.code,
				    title: "万岳同城跑腿",
				    summary: "我正在使用万岳同城跑腿，赶紧跟我一起来体验！",
				    imageUrl: getApp().globalData.weburl + '/logo.png',
				    success: function (res) {
				        console.log("success:" + JSON.stringify(res));
				    },
				    fail: function (err) {
				        console.log("fail:" + JSON.stringify(err));
				    }
				});
			},
			doHaibao(){
				uni.navigateTo({
					url:'./invaltionImageView'
				})
			}
		}
	}
</script>

<style>
	page {
		/* width: 100%; */
		/* height: 100%; */
		background-color: #F7F8F9;

	}

	.content {
		width: 100%;
		height: 100%;
	}

	.inval-scroll {
		position: absolute;
		width: 100%;
	}

	.inval-middleView {
		/* position: absolute; */
		margin-left: 12px;
		width: calc(100% - 24px);
		height: 307px;
		border-radius: 8px;
		background-color: #FFFFFF;
		display: flex;
	}

	.inval-tipView {
		/* position: absolute; */
		margin-top: 8px;
		margin-left: 12px;
		width: calc(100% - 24px);
		min-height: 200px;
		border-radius: 8px;
		background-color: #FFFFFF;
		display: flex;
		/* margin-bottom: 20px; */
	}

	.inval-tipLabel {
		margin-left: 12px;
		margin-top: 10px;
		padding-bottom: 12px;
		width: calc(100%-24px);
		line-height: 17px;
		font-size: 14px;
		color: #646464;
	}
</style>
